import { useEffect, useRef } from 'react';
import CustomBreadcrumb from '../../../components/CustomBreadcrumb';
import { DescribeP, DIV, KeyTag } from '../../../components/Div';
import UseIntroduce from '../../../components/UseIntroduce';
import styled from 'styled-components';
import { dataList } from './dataList'
import './index.scss'

const Wrapper = styled.div`
  border: 1px solid red;
  height: 460px;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
`
const Title = styled.div`
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  font-weight: 700;
  padding-left: 20px;
  border-bottom: 1px solid #ccc;
`
const MessageList = styled.div`
  height: 400px;
  overflow-y: auto;
  margin: 10px 20px;
  flex: 1;
`
const comeName = [
  {
    id: 1,
    name: '',
    text: ''
  }
]
const newList = comeName.concat(dataList)

const Interactive = () => {
  const bottomRef = useRef<any>(null)
  const scrollToBottom = () => {
    if (bottomRef && bottomRef.current) {
      bottomRef.current.scrollIntoView(false);
    }
  }
  useEffect(() => {
    scrollToBottom();
  }, [bottomRef]);

  const describe =
    <DescribeP>
      <DIV>1</DIV>
      <DIV>2) 
        <DIV>1</DIV>
        <DIV>2<KeyTag></KeyTag></DIV>
        <DIV>3<KeyTag></KeyTag></DIV>
        <DIV>4<KeyTag></KeyTag></DIV>
      </DIV>
    </DescribeP>

  return (
    <>
      <CustomBreadcrumb arr={['', '']} />
      <UseIntroduce describe={describe} />
      <Wrapper>
        <Title></Title>
        <MessageList>
          {
            newList.map(item => {
              const name = (item.name).substring(0, 2) + '****' + (item.name).substring(4, item.name.split('').length)
              return (
                <div key={`${item.name}-${item.id}`} className='content'>
                  <div className='name'>
                    {name} {item.text ? ':' : ''}
                    <span className='text'>{item.text}</span>
                  </div>
                </div>
              )
            })
          }
          <div ref={bottomRef} />
        </MessageList>
      </Wrapper>
    </>
  );
}

export default Interactive;
