import { Typography } from 'antd';
import CustomBreadcrumb from '../../../components/CustomBreadcrumb';
import { DescribeP, DIV, KeyTag } from '../../../components/Div';
import IconFont from '../../../components/IconFont';
import UseIntroduce from '../../../components/UseIntroduce';
import Select1 from './blocks/Select1';
import Select2 from './blocks/Select2';
import Select3 from './blocks/Select3';


const SelectDisable = () => {
  const describe = <div>
    <DescribeP> antd <Typography.Link href='https://ant.design/components/select-cn/' target="_blank"><KeyTag>Select<IconFont type="icon-sousuo" /></KeyTag></Typography.Link> </DescribeP>
    <DIV>1<KeyTag></KeyTag>option</DIV>
    <DIV>2maxTagCount:  <KeyTag>tags</KeyTag></DIV>
    <DIV>3isAllowClear:  <KeyTag></KeyTag></DIV>
    <DIV>4<KeyTag>width: 300</KeyTag>number</DIV>

  </div>;
  return (
    <>
      <CustomBreadcrumb arr={['', '']} />
      <UseIntroduce describe={describe} />
      {/*  */}
      <Select1 />
      {/*  */}
      <Select2 />
      {/*  */}
      <Select3 />
    </>
  );
}

export default SelectDisable;
